Berikut link untuk info lengkap dari acara kami :\n
${website.value}${NamaUndangan.value.replace(/ /g,"+")}\n\n
Merupakan suatu kebahagiaan bagi kami apabila Bapak/Ibu/Saudara/i berkenan untuk hadir dan memberikan doa restu.\r
Jangan lupa isi Ucapan dan Konfirmasi kehadiran di *Wedding Wish dan RSVP* ya, Terima Kasih..\n
*Wassalamu'alaikum Warahmatullah Wabarakatuh*\n
Hormat kami,\r
*Devinta Chen Alif & Ardea Bagas Wibisono*\n
(Salin link dan buka di browser bila link tidak dapat dibuka, usahakan mematikan fitur dark mode dalam browser untuk hasil yang maksimal)`;
// navigator.clipboard.writeText(Template);
Clipboard.copy(Template);

